.container {
    width: 18vw;
    height: 1px;
    min-height: calc(100vh);
    position: fixed;
    left: 0;
    top: 0;

    background-color: rgba(43, 44, 55, 0.85);
    border-right: 1px solid rgba(255, 255, 255, 0.09);

    .innerWrapper {
        padding: 10px 10px 10px 0;
        height: 100%;
        display: flex;
        flex-direction: column;

        .logoContainer {
            padding-left: 5vw;
            margin-bottom: 50px;
        }
    }
  }